@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
	body {
		font-family: 'VT323', monospace;
	}
}

@layer utilities {
	.pixel, .pixel2, .pixel3, .pixel4 {
		font-size: 25px;
		color: white;
		height: auto;
		margin: 10px;
		font-family: 'VT323';
		
		position: relative;
		display: inline-block;
		vertical-align: top;
		text-transform: uppercase;
		
		cursor: pointer;
		
		-webkit-touch-callout: none;
		-webkit-user-select: none;
		-khtml-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
	  }
	  
	  .pixel:active, .pixel2:active, .pixel3:active, .pixel4:active {
		top: 2px;
	  }
	  
	  .pixel {
		line-height: 0;
		
		image-rendering: optimizeSpeed;
		image-rendering: -moz-crisp-edges; /* Firefox */
		image-rendering: -o-crisp-edges; /* Opera */
		image-rendering: -webkit-optimize-contrast; /* Webkit (non-standard naming) */
		image-rendering: crisp-edges;
		-ms-interpolation-mode: nearest-neighbor; /* IE (non-standard property) */
		
		border-style: solid;
		border-width: 20px;
		-moz-border-image: url(https://i.imgur.com/sREM8Yn.png) 20 stretch;
		-webkit-border-image: url(https://i.imgur.com/sREM8Yn.png) 20 stretch;
		-o-border-image: url(https://i.imgur.com/sREM8Yn.png) 20 stretch;
		border-image: url(https://i.imgur.com/sREM8Yn.png) 20 stretch;
	  }
	  
	  .pixel p {
		display: inline-block;
		vertical-align: top;
		position: relative;
		width: auto;
		text-align: center;
		margin: -20px -20px;
		line-height: 20px;
		padding: 10px 20px;
		
		  background: #000000;
		  background:
			  linear-gradient(135deg, transparent 10px, #000000 0) top left,
			  linear-gradient(225deg, transparent 10px, #000000 0) top right,
			  linear-gradient(315deg, transparent 10px, #000000 0) bottom right,
			  linear-gradient(45deg,  transparent 10px, #000000 0) bottom left;
		  background-size: 50% 50%;
		  background-repeat: no-repeat;
		  background-image:
			  radial-gradient(circle at 0 0, rgba(204,0,0,0) 14px, #000000 15px),
			  radial-gradient(circle at 100% 0, rgba(204,0,0,0) 14px, #000000 15px),
			  radial-gradient(circle at 100% 100%, rgba(204,0,0,0) 14px, #000000 15px),
			  radial-gradient(circle at 0 100%, rgba(204,0,0,0) 14px, #000000 15px);
	  }
	  
	  .pixel2 {
		position: relative;
		display: block;
		margin: 10px;
		font-family: 'VT323';
		text-transform: uppercase;
		
		font-size: 40px;
		color: white;
	  }
	  
	  .pixel2::before {
		content: "";
		display: block;
		position: absolute;
		top: 10px;
		bottom: 10px;
		left: -10px;
		right: -10px;
		background: black;
		z-index: -1;
	  }
	  
	  .pixel2::after {
		content: "";
		display: block;
		position: absolute;
		top: 4px;
		bottom: 4px;
		left: -6px;
		right: -6px;
		background: black;
		z-index: -1;
	  }
	  
	  .pixel2 {
		padding: 10px 10px;
		position: relative;
		background: black;
		width: auto;
		z-index: 2;
	  }

	  .pixel3 {
		position: relative;
		display: block;
		margin: 10px;
		font-family: 'VT323';
		text-transform: uppercase;
		
		font-size: 20px;
		color: white;
	  }
	  
	  .pixel3::before {
		content: "";
		display: block;
		position: absolute;
		top: 10px;
		bottom: 10px;
		left: -10px;
		right: -10px;
		background: black;
		z-index: -1;
	  }
	  
	  .pixel3::after {
		content: "";
		display: block;
		position: absolute;
		top: 4px;
		bottom: 4px;
		left: -6px;
		right: -6px;
		background: black;
		z-index: -1;
	  }
	  
	  .pixel3 {
		padding: 10px 10px;
		position: relative;
		background: black;
		width: auto;
		z-index: 2;
	  }


	  .pixel4 {
		position: relative;
		display: block;
		margin: 10px;
		font-family: 'VT323';
		text-transform: uppercase;
		
		font-size: 13px;
		color: white;
	  }
	  
	  .pixel4::before {
		content: "";
		display: block;
		position: absolute;
		top: 10px;
		bottom: 10px;
		left: -10px;
		right: -10px;
		background: black;
		z-index: -1;
	  }
	  
	  .pixel4::after {
		content: "";
		display: block;
		position: absolute;
		top: 4px;
		bottom: 4px;
		left: -6px;
		right: -6px;
		background: black;
		z-index: -1;
	  }
	  
	  .pixel4 {
		padding: 10px 10px;
		position: relative;
		background: black;
		width: auto;
		z-index: 2;
	  }
}

.ReactCollapse--collapse {
	transition:height 500ms;
}